<template>
  <div id="app">
    <h1>Procesar Archivo Excel</h1>
    <form @submit.prevent="submitFile">
      <input type="file" ref="fileInput" accept=".xlsx, .xls" />
      <button type="submit">Procesar Archivo</button>
    </form>

    <p v-if="loading">Procesando archivo, por favor espere...</p>
    <p v-if="error" style="color: red;">{{ error }}</p>
  </div>
</template>

<script>
export default {
  data() {
    return {
      loading: false,
      error: null,
    };
  },
  methods: {
    async submitFile() {
      const file = this.$refs.fileInput.files[0];

      if (!file) {
        this.error = "Por favor, selecciona un archivo.";
        return;
      }

      this.loading = true;
      this.error = null;

      const formData = new FormData();
      formData.append("file", file);

      try {
        const response = await fetch("http://127.0.0.1:5000/procesar_excel", {
          method: "POST",
          body: formData,
        });

        if (!response.ok) {
          throw new Error("Error al procesar el archivo.");
        }

        // Descargar el archivo modificado
        const blob = await response.blob();
        const url = window.URL.createObjectURL(blob);
        const link = document.createElement("a");
        link.href = url;
        link.setAttribute("download", file.name.replace(/\.[^/.]+$/, "_Modificado.xlsx"));
        document.body.appendChild(link);
        link.click();
        document.body.removeChild(link);
      } catch (err) {
        this.error = err.message;
      } finally {
        this.loading = false;
      }
    },
  },
};
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  text-align: center;
  margin-top: 60px;
}

input[type="file"] {
  margin-bottom: 20px;
}

button {
  padding: 10px 20px;
  background-color: #42b983;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

button:hover {
  background-color: #369f6e;
}
</style>